let courr = 10
let setId = null
export default {
    data(){
        return {
            phone:'',
            code:'',
            clickText:'获取验证码'
        }
    },
    methods: {
        geteditphonecode(){
            if(this.phone.length != 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            if(setId) {
                // 禁止重复发送 
                return false
            }
            this.$http.geteditphonecode({phone:this.phone}).then(res=>{
                if(res.status == 200) {
                   this.$toast(res.mess)
                   setId = setInterval(()=>{
                       courr--
                       if(courr == 0) {
                           courr = 60
                           clearInterval(setId)
                           this.clickText = '重新获取'
                           
                           return false
                       }
                       this.clickText = courr + 's'
                   },1000)
                }
            })
        },
        confirm(){
            if(this.phone.length != 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            if(this.code.length == '') {
                this.$toast('请输入验证码')
                return false
            }
            this.$http.geteditphone({
                phone:this.phone,
                phonecode: this.code
            }).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    setTimeout(()=>{
                        this.getback()
                    },1500)
                }
            })
        }
    },
}
